// mouse icon by mioe (2019-02-02)
@keyframes scroll {
  0% {
    opacity: 1;
    transform: translateY(0px);
  }
  100% {
    opacity: 0;
    transform: translateY(10px);
  }
}

.scrollMe {
  position: absolute;
  display: block;
  top: calc(100% - 60px);
  left: 50%;
  transform: translateX(-50%);
}

.mouse {
  position: relative;
  border: 2px solid #FFDB18;
  border-radius: 13px;
  height: 42px;
  width: 26px;
  &::after {
    content: '';
    position: absolute;
    top: 4px;
    left: calc(50% - 4px);
    width: 8px;
    height: 8px;
    border-radius: 50%;
    animation-duration: 2s;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
    animation-timing-function: ease;
    animation-name: scroll;
    background: none repeat scroll 0 0 #C4C4C4;
  }
}

//////////////////////////////////////////////////////////////////////
.scrollMe {
  display: flex;
  align-items: center;
  p {
    margin: 0 10px;
    color: #C4C4C4;
  }
}

.btn-keyboard {
  position: relative;
  width: 26px;
  height: 26px;
  border: 2px solid #FFDB18;
  border-radius: 9px;
  i {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: #C4C4C4;
  }
}

@media (max-width: 684.98px) {
  .scrollMe {
    .mouse, p {
      display: none;
    }
  }
}