//////////////////////////////////////////////////////////////////////
// fonts
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700');
//////////////////////////////////////////////////////////////////////
// imports
@import "_variables.scss";
@import "_fullpage.scss";
@import "_scrollMe.scss";
//////////////////////////////////////////////////////////////////////
// default by mioe
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  // reset
  padding: 0;
  margin: 0;
  text-decoration: none;
  color: $bg-text-white;
  &:hover {
    text-decoration: none;
  }
}

ul {
  // reset
  padding: 0;
  margin: 0;
  li {
    padding: 0;
    margin: 0;
  }
}

input,
textarea,
button,
select {
  border: none;
  background: #eee;
}

.dontTouchMe {
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
//////////////////////////////////////////////////////////////////////
// global styles
html {
  background: $bg-main-black;
  font-family: 'Roboto', sans-serif;
  color: $bg-text-white;
  font-size: 18px;
  font-weight: $light;
  line-height: 1.2;
  h1 {
    font-weight: 500;
    font-size: 72px;
    line-height: 1;
    span { color: #FFDB18; }
  }
  h2 {
    font-weight: 400;
    font-size: 20px;
    display: block;
    padding-left: 15px;
    position: relative;
    text-transform: uppercase;
    margin-bottom: 35px;
    &::before {
      content: '';
      left: 0;
      position: absolute;
      height: 100%;
      width: 2px;
      background: #FFDB18;
    }
  }
  h3 {
    font-weight: 400;
    font-size: 48px;
  }
}

@media (min-width: 992px) and (max-width: 1649.98px) {
  html {
    font-size: 16px;
  }

  html {
    h2 {
      font-size: 18px;
      margin-bottom: 20px;
    }
    h3 {
      font-size: 30px;
    }
  }
}

@media (max-width: 991.98px) {
  html {
    font-size: 14px;
  }

  html {
    h2 {
      font-size: 16px;
      margin-bottom: 10px;
    }
    h3 {
      font-size: 24px;
    }
  }
}

.btn-yellow-default {
  background: #FFDB18;
  border-radius: 20px;
  padding: 12px 30px; 
  box-shadow: 0 0 0 rgba(255, 255, 255, .5);
  transition: .3s;
  cursor: pointer;
  font-weight: 500;
  display: inline-block;
  color: #000;
  i {
    color: #FFDB18;
    margin-right: 10px;
  }
  &:hover {
    box-shadow: 0 0 15px rgba(255, 255, 255, .5);
  }
}

.btn-gray-default {
  display: flex;
  background: #334551;
  border-radius: 20px;
  padding: 8px 15px; 
  box-shadow: -1px -1px -1px rgba(255, 255, 255, .5);
  transition: .3s;
  cursor: pointer;
  i {
    color: #FFDB18;
    margin-right: 10px;
  }
  &:hover {
    box-shadow: 0 0 15px rgba(255, 255, 255, .5);
  }
}

.bg {
  position: relative;
  &::after {
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    opacity: .2;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
  }
  &:nth-child(2n) {
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.5);
    z-index: 1;
  }
}
.bg-welcome::after { background-image: url('../img/bg-welcome.jpg'); }
.bg-sale::after { background-image: url('../img/bg-sale.jpg'); }
.bg-contact::after { background-image: url('../img/bg-contact.jpg'); }

.block {
  position: relative;
  width: 1600px;
  max-height: 800px;
  // background: $select-green;
  margin: 0 auto;
}

@media (min-width: 1250px) and (max-width: 1649.98px) {
  .block {
    width: 1140px;
  }
}
@media (min-width: 992px) and (max-width: 1249.98px) {
  .block {
    width: 960px;
  }
}
@media (min-width: 768px) and (max-width: 991.98px) {
  .block {
    width: 720px;
  }
}
@media (min-width: 576px) and (max-width: 767.98px) {
  .block {
    width: 540px;
  }
}
@media (max-width: 575.98px) {
  .block {
    width: 90%;
  }
}
//////////////////////////////////////////////////////////////////////
// toolbar
.fixed-top {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  z-index: 999;
}

.toolbar {
  position: relative;
  width: 1600px;
  height: 100%;
  margin: 0 auto;
}

.toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &-logo img {
    display: block;
  }
  &-right, &-left, &-center {
    display: flex;
    align-items: center;
  }
  &-phone {
    display: flex;
    align-items: center;
    margin-left: 50px;
    cursor: pointer;
    font-weight: $medium;
    i {
      margin-right: 10px;
      font-size: 24px;
    }
    i,p {
      transition: 0.3s;
    }
    &:hover {
      i,p {
        color: #FFDB18;
      }
    }
  }
  &-center {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    i {
      margin: 0 10px;
      transition: 0.3s;
      cursor: pointer;
      font-size: 24px;
    }
    i:hover {
      color: #FFDB18;
    }
  }
  &-right {
    margin-right: 15px;
    .btn-gray-default {
      margin-right: 50px;
    }
  }
  &-menu {
    i {
      font-size: 35px;
      cursor: pointer;
      transition: $anime;
      &:hover {
        color: #FFDB18;
      }
    }
  }
}

@media (min-width: 992px) and (max-width: 1249.98px) {
  .toolbar {
    &-center {
      position: relative;
      left: auto;
      transform: translateY(0);
    }
  }
}

@media (min-width: 1250px) and (max-width: 1649.98px) {
  .fixed-top {
    height: 85px;
  }

  .toolbar {
    width: 1140px;
    &-logo { 
      height: 100%;
      padding: 20px 0;
      img {
        height: 60px;
      }
    }
  }
}

@media (min-width: 992px) and (max-width: 1249.98px) {
  .fixed-top {
    height: 70px;
    background: $bg-main-black;
    box-shadow: $shadow;
  }

  .toolbar {
    width: 960px;
    &-logo { 
      height: 100%;
      padding: 20px 0;
      img {
        height: 50px;
      }
    }
  }
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .fixed-top {
    height: 60px;
    background: $bg-main-black;
    box-shadow: $shadow;
  }

  .toolbar {
    width: 720px;
    &-center {
      top: 100%;
      left: 70px;
      background: $bg-main-black;
      height: 75%;
      padding: 0 20px;
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
      box-shadow: $shadow;
      i {
        font-size: 18px;
      }
    }
    &-logo { 
      height: 100%;
      padding: 20px 0;
      img {
        height: 40px;
      }
    }
    &-menu {
      i {
        font-size: 28px;
      }
    }
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .fixed-top {
    height: 50px;
    background: $bg-main-black;
    box-shadow: $shadow;
  }

  .toolbar {
    width: 540px;
    &-center {
      top: 100%;
      left: 70px;
      background: $bg-main-black;
      height: 75%;
      padding: 0 20px;
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
      box-shadow: $shadow;
      i {
        font-size: 18px;
      }
    }
    &-phone {
      display: none;
    }
    &-logo { 
      height: 100%;
      padding: 20px 0;
      img {
        height: 40px;
      }
    }
    &-menu i { 
      font-size: 24px;
    }
  }

  .toolbar-right .btn-gray-default {
    margin-right: 20px;
  }

  .toolbar-phone {
    margin-left: 12px
  }
}

@media (max-width: 575.98px) {
  .fixed-top {
    height: 50px;
    background: $bg-main-black;
    box-shadow: $shadow;
  }

  .toolbar {
    width: 90%;
    &-phone {
      display: none;
    }
    &-center {
      top: 100%;
      background: $bg-main-black;
      height: 75%;
      padding: 0 20px;
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
      box-shadow: $shadow;
      i {
        font-size: 18px;
      }
    }
    &-logo { 
      height: 100%;
      padding: 20px 0;
      img {
        height: 40px;
      }
    }
    &-menu i { 
      font-size: 24px;
    }
  }

  .toolbar-right .btn-gray-default {
    margin-right: 20px;
    i {
      margin: 0 10px;
    }
    p {
      display: none;
    }
  }

  .toolbar-phone {
    margin-left: 12px
  }
}
