//about
.about-grid {
  margin-top: 60px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 80px;
  &-left {
    display: flex;
    justify-content: flex-end;
  }
  &-right {
    display: flex;
    justify-content: flex-start;
  }
}

.plus-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.plus-box {
  position: relative;
  width: 490px;
  height: 130px;
  background: #334551;
  transition: .3s;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 30px;
  cursor: pointer;
  p {
    font-size: 24px;
  }
  &.active{
    padding: 0;
    z-index: 1;
    width: 570px;
    height: 190px;
    background: #FFDB18;
    span,p {
      color: #000;
    }
  }
  span {
    font-weight: 400;
    font-size: 46px;
  }
}

@media (min-width: 1250px) and (max-width: 1649.98px) {
  .block-about {
    padding-top: 50px;
  }
  .about-grid {
    margin-top: 30px;
  }

  .plus-box {
    width: 400px;
    height: 130px;
    span {
      font-size: 40px;
    }
    p {
      font-size: 20px;
    }
    &.active{
