/////////////////////////////////////
//sale
.block-sale {
  margin: 40px auto 40px auto;
  .details {
    margin-top: 50px;
    text-align: right;
  }
}

.sale-grid {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.sale-box {
  margin: 70px 50px 0 50px;
  width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
  &-top {
    background: #FFDB18;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    width: 100%;
    padding: 60px 0;
    text-align: center;
    p {
      font-weight: 700;
      font-size: 48px;
      color: #493A3A;
    }
  }
  &-bottom {
    background: #334551;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    width: 100%;
    padding: 30px 50px;
    p {
      font-weight: 300;
      font-size: 28px;
    }
  }
  &:nth-child(2n) {
    flex-direction: column-reverse;
    .sale-box-top {
      border-radius: 0;
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
    }
    .sale-box-bottom {
      border-radius: 0;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
    }
  }
}

@media (min-width: 1250px) and (max-width: 1649.98px) {
  .block-sale {
    padding-top: 50px;
  }
  .sale-box {
    margin: 50px 30px 0 30px;
    &-top {
      padding: 45px 0;
      p {
        font-size: 36px;
      }
    }
    &-bottom {
      padding: 30px 55px;
      p {
        font-size: 20px;
      }
    }
  }
}

@media (min-width: 992px) and (max-width: 1249.98px) {
  .sale-box {
    margin: 30px 15px 0 15px;
    &-top {
      padding: 35px 0;
      p {
        font-size: 36px;
      }
    }
    &-bottom {
      padding: 20px 30px;
      p {
        font-size: 20px;
      }
    }
  }
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .sale-box {
    margin: 30px 15px 0 15px;
    &-top {
      padding: 35px 0;
      p {
        font-size: 28px;
      }
    }
    &-bottom {
      padding: 20px 30px;
      p {
        font-size: 18px;
      }
    }
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .sale-grid {
    flex-direction: column;
  }
  .sale-box {
    height: 160px;
    margin: 30px 0 0 0;
    flex-direction: row;
    &-top {
      height: 100%;
      width: 100%;
      padding: 0;
      border-top-left-radius: 20px;
      border-top-right-radius: 0;
      border-bottom-left-radius: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      p {
        font-size: 28px;
      }
    }
    &-bottom {
      height: 100%;
      padding: 20px 30px;
      border-top-right-radius: 20px;
      border-bottom-right-radius: 20px;
      border-bottom-left-radius: 0;
      p {
        font-size: 16px;
      }
    }
    &:nth-child(2n) {
      flex-direction: row-reverse;
      .sale-box-top {
        border-top-left-radius: 0;
        border-top-right-radius: 20px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 20px;
      }
      .sale-box-bottom {
        border-top-left-radius: 20px;
        border-top-right-radius: 0;
        border-bottom-left-radius: 20px;
        border-bottom-right-radius: 0;
      }
    }
  }
}

@media (max-width: 575.98px) {
  .block-sale {
    margin: 40px auto;
  }
  
  .sale-grid {
    flex-direction: column;
    margin-bottom: 10px;  
  }
  .sale-box {
    width: 300px;
    height: 160px;
    margin: 30px 0 0 0;
    flex-direction: row;
    &-top {
      height: 100%;
      width: 100%;
      padding: 0;
      border-top-left-radius: 20px;
      border-top-right-radius: 0;
      border-bottom-left-radius: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      p {
        font-size: 24px;
      }
    }
    &-bottom {
      height: 100%;
      width: 100%;
      padding: 12px 20px;
      border-top-right-radius: 20px;
      border-bottom-right-radius: 20px;
      border-bottom-left-radius: 0;
      p {
        font-size: 16px;
      }
    }
    &:nth-child(2n) {
      flex-direction: row-reverse;
      .sale-box-top {
        border-top-left-radius: 0;
        border-top-right-radius: 20px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 20px;
      }
      .sale-box-bottom {
        border-top-left-radius: 20px;
        border-top-right-radius: 0;
        border-bottom-left-radius: 20px;
        border-bottom-right-radius: 0;
      }
    }
  }
}
/////////////////////////////////////
//service
.block-service {
  .servicePhoto {
    margin: 40px 0;
    width: 100%;
    height: 125px;
    background-image: url('../img/servicePhoto.jpg');
    opacity: .25;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
