  &-right {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: right;
    .btn-gray-default {
      margin: 10px 0;
      border-radius: 55px;
      font-size: 16px;
      i {
        font-size: 22px;
      }
    }
    span {
      color: #FFDB18;
      font-size: 26px;
    }
  }
}

@media (min-width: 1250px) and (max-width: 1649.98px) {
  .block-service {
    padding-top: 50px;
  }
}

@media (min-width: 992px) and (max-width: 1249.98px) {
  .hidden-list {
    grid-template-columns: 1fr .5fr;
  }
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .hidden-list {
    grid-template-columns: 1fr .5fr;
    grid-column-gap: 150px;
  }
}

@media (max-width: 767.98px) {
  .block-service {
    margin: 40px auto;
  }

  .hidden-list {
    grid-template-columns: 1fr;
    grid-column-gap: 20px;
  }
  .hidden-list-right {
    margin-top: 20px;
    text-align: left;
  }
}

/////////////////////////////////////
//contact
.contact-grid {
  min-height: 530px;
  position: relative;
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
  p {
    font-weight: 300;
    font-size: 26px;
  }
  &-left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .contact-top {
    p {
      margin-bottom: 50px;
    }
    a {
      color: #FFDB18;
    }
  }
 
}
.contact-grid-right {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1;
  padding: 80px 70px;
  min-width: 600px;
  &::after {
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    opacity: .75;
    background: #212A30;
    border-radius: 10px;
    z-index: -1;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  }
  .form {
    display: flex;
    flex-direction: column;
    h3 {
      text-align: center;
      margin-bottom: 60px;
    }
    input[type="text"],
    input[type="tel"] {
      font-family: 'Roboto';
      font-weight: 300;
      font-size: 26px;
      background: none;
      color: #fff;
      border-bottom: 1px solid rgba(255, 255, 255, 0.507);
      margin-bottom: 30px;
      padding: 10px 0;
    }
    .more {
      display: flex;
      justify-content: space-between;
      margin-top: 50px;
      label {
        display: flex;
