// bg-colors
$bg-main-black: #212a30;
$bg-main-yellow: #FFDB18;
$bg-main-white: #f1f1f1;
$bg-text-white: #fafafa;
$bg-gray: #C4C4C4;

// dev select
$select-green: rgba($color: yellowgreen, $alpha: .2);

// shadows
$shadow: 0 3px 5px rgba(0,0,0,.15);

// transition
$anime: .3s ease-in-out;

// fonts-size
$thin: 100;
$extra-light: 200;
$light: 300;
$regular: 400;
$medium: 500;
$semi-bold: 600;
$bold: 700;
$extra-bold: 800;
$black-font: 900;

// gradients
$reef: linear-gradient(to right, #00bae4, #3a7bd5);